import { Carousel, Container, Image } from 'react-bootstrap';
import { getBannerServer } from '@/services/index';

import './index.less';

const whereParamsBanner = {
  channel: 'limeetpet',
  type: 'home'
};

const getBannerFetch = async ({ lang }) => {
  const projectId = 1747727677;
  const language = lang;
  try {
    const result = await getBannerServer({ projectId, ...whereParamsBanner, language });
    if (result && result.status == 200 && result.data && result.data && result.data.rows) {
      return result.data.rows;
    } else {
      return [];
    }
  } catch (err) {
    console.log(err);
  }
};

async function BannerSwiperServer({ lang }) {
  const swiperBanner = await getBannerFetch({ lang });
  const renderSwiperHtml = () => {
    const html = [];
    // eslint-disable-next-line @typescript-eslint/no-unused-expressions
    swiperBanner &&
      swiperBanner.length &&
      swiperBanner.map((item, idx) => {
        if (item.is_show && item.src) {
          html.push(
            <div className="carousel-slide" style={{ backgroundColor: '#ff5733' }} key={idx} title={item.name}>
              <a href={item.url} target='_blank' title={item.name} ><Image src={item.src} fluid /> </a>
            </div>
          );
        }
      });
    return html;
  };

  return (
    <Container fluid className='banner-swiper'>
      <div id="carousel2" className="carousel-container">
        <div className="carousel">
          <div className="carousel-inner">
            <div className="carousel-item">
              <Image src="https://picsum.photos/800/600?random=10" alt="城市风光" fluid></Image>
            </div>
            <div className="carousel-item">
              <Image src="https://picsum.photos/800/600?random=11" alt="自然风光" fluid></Image>
            </div>
            <div className="carousel-item">
              <Image src="https://picsum.photos/800/600?random=12" alt="人文景观" fluid></Image>
            </div>
          </div>

          <div className="carousel-indicators">
            <div className="indicator active" data-index="0"></div>
            <div className="indicator" data-index="1"></div>
            <div className="indicator" data-index="2"></div>
          </div>
        </div>
      </div>
    </Container>
  );
}
export default BannerSwiperServer;
